.prize{
    min-width: 100px;
    aspect-ratio: 39 / 89;
    background-image: url(../../img/prize/prize_logo.png);
    background-repeat: no-repeat;
    width: 8lvw;
    height: auto;
    background-clip: padding-box;
    background-size: contain;
    overflow: hidden;
    min-height: auto;
    display: inline-block;
    border-radius: .2rem;
    position: absolute;
    bottom: 0px;
    margin: 2rem;
    box-shadow: 0 0 16px #25131721, 0 0 10px hsl(347deg 38% 13% / 18%), 0px 0px 3px hsl(347deg 32% 16% / 12%);
    scale:1;
    z-index:1;
    transition: all .3s ease;
  }
  .prize::after{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(210deg, #00000070 0%, #0000 40%);
    z-index: 1;
    border-radius: .2rem;
    transition: background .3s ease;
  }
  
  .prize:hover{
    scale:1.003;
}
.prize:hover:after{
  background: linear-gradient(210deg, #00000000 0%, #ede9e720 100%)}

@media screen and (max-width: 424px) {.prize{min-width: 80px;
margin:1rem;}}